.back-button {
    background-color: #99cfff;
    font-family: 'Helvetica Neue', sans-serif;
    border: none;
    padding: 10px 20px;
    font-size: 1rem;
    border-radius: 6px;
    cursor: pointer;
    margin-bottom: 1rem;
    transition: background-color 0.2s ease;
  }
  
  .back-button:hover {
    background-color: #d9effc;
  }